.container {
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(0,0,0,0.4);
}

.model {
    border-radius: 5px;
    padding: 2rem;
    background-color: white;
    width: 20em;
}

.model .btn {
    margin-top: 1rem;
    border-radius: 10px;
    border: none;
    background-color: #1d4ed8;
    color: #fff;
    padding: 0.5rem 1rem;
    cursor: pointer;
    box-shadow: 0px 5px 5px #ccc;
    display: block;
    margin: auto;
}

.form-group {
    display: flex;
    flex-direction: column;
    margin-bottom: 1rem;
}

.form-group input,
.form-group select {
    border : 1px solid black;
    border-radius: 0.3rem;
    padding: 0.3rem;
    font-size: 1rem;    
}

.form-group label {
    margin-bottom: 0.2rem;
}
